.about {
  width: 100%;
  background: #23263a;
  padding: 100px 0;
}

.about__img {
  padding: 30px;
}

.about__img img {
  width: 300px;
  height: 400px;
}

.about__info {
  color: #fff;
}

.about__info h1 {
  margin-bottom: 30px;
}

.about__info-p1 {
  font-size: 14px;
  margin-bottom: 20px;
}

.about__info-p2 {
  font-size: 14px;
}

.info__contacts {
  margin-top: 20px;
}

.info__contacts strong {
  color: #ff4a57;
}

.info__contacts p {
  margin-top: 3px;
}

.info__contacts .col-6 {
  margin: 10px 0;
}

@media screen and (max-width: 765px) {
  .about__img {
    text-align: center;
  }
}
